import { http } from '../../../utils/http';
import { setMessage } from '../MessageActions';
import {
    GET_ALL_PARENT_CATEGORY_START,
    GET_ALL_PARENT_CATEGORY_SUCCESS,
    GET_ALL_PARENT_CATEGORY_ERROR,
    GET_ALL_CATEGORY_START,
    GET_ALL_CATEGORY_SUCCESS,
    GET_ALL_CATEGORY_ERROR
} from '../../constants';

const getAllParentCategoryStart = () => {
    return { type: GET_ALL_PARENT_CATEGORY_START };
};
const getAllParentCategorySuccess = (payload) => {
    return { type: GET_ALL_PARENT_CATEGORY_SUCCESS, payload };
};
const getAllParentCategoryError = (error) => {
    return { type: GET_ALL_PARENT_CATEGORY_ERROR, error };
};

const getAllCategoryStart = () => {
    return { type: GET_ALL_CATEGORY_START };
};
const getAllCategorySuccess = (payload) => {
    return { type: GET_ALL_CATEGORY_SUCCESS, payload };
};
const getAllCategoryError = (error) => {
    return { type: GET_ALL_CATEGORY_ERROR, error };
};

const addParentCategory = (payload, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_BOOK, 'POST', `books-service/parent-categories`, payload, true)
            .then((res) => {
                dispatch(setMessage(res?.message));
                successCB && successCB(res);
            })
            .catch((err) => {
                console.log(err);
            });
    } catch (error) {
        console.log(error);
    }
};

const updateParentCategory = (payload, id, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_BOOK, 'PUT', `books-service/parent-categories/${id}`, payload, true)
            .then((res) => {
                if (res) {
                    dispatch(setMessage(res?.message));
                    successCB && successCB(res);
                }
            })
            .catch((err) => {
                console.log(err);
            });
    } catch (error) {
        console.log(error);
    }
};

const getAllParentCategory =
    (rowsPerPage = 10, searchData = '', page = 1) =>
    (dispatch) => {
        dispatch(getAllParentCategoryStart());
        try {
            http(
                process.env.REACT_APP_ROOT_URL_BOOK,
                'GET',
                `books-service/parent-categories?searchKey=${searchData}&pageNumber=${page}&pageSize=${rowsPerPage}`,
                true
            )
                .then((res) => {
                    const payload = {
                        allParentCategories: res
                    };
                    dispatch(getAllParentCategorySuccess(payload));
                })
                .catch((err) => {
                    dispatch(getAllParentCategoryError(err));
                    dispatch(setMessage(err?.message));
                });
        } catch (error) {
            dispatch(getAllParentCategoryError('Something went wrong'));
        }
    };

const addCategory = (payload, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_BOOK, 'POST', `books-service/categories`, payload, true)
            .then((res) => {
                dispatch(setMessage(res?.message));
                successCB && successCB(res);
            })
            .catch((err) => {
                console.log(err);
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        console.log(error);
    }
};

const updateCategory = (payload, id, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_BOOK, 'PUT', `books-service/categories/${id}`, payload, true)
            .then((res) => {
                if (res) {
                    dispatch(setMessage(res?.message));
                    successCB && successCB(res);
                }
            })
            .catch((err) => {
                console.log(err);
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        console.log(error);
    }
};

const getAllCategory =
    (rowsPerPage = 10, searchData = '', page = 1) =>
    (dispatch) => {
        dispatch(getAllCategoryStart());
        try {
            http(
                process.env.REACT_APP_ROOT_URL_BOOK,
                'GET',
                `books-service/categories?searchKey=${searchData}&pageNumber=${page}&pageSize=${rowsPerPage}`,
                true
            )
                .then((res) => {
                    const payload = {
                        allSubCategories: res
                    };
                    dispatch(getAllCategorySuccess(payload));
                })
                .catch((err) => {
                    dispatch(getAllCategoryError(err));
                    dispatch(setMessage(err?.message));
                });
        } catch (error) {
            dispatch(getAllCategoryError('Something went wrong'));
        }
    };

export { updateParentCategory, getAllParentCategory, addParentCategory, addCategory, updateCategory, getAllCategory };
