import React from 'react';
import CloseIcon from '@material-ui/icons/Close';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { FormProvider, RHFTextField, RHFSelect } from '../../../../components/hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { addCategory, updateCategory, getAllCategory } from '../../../../store/actions/BookCategoryActions';
import useStyles from '../styles';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography, Grid, Link, Divider } from '@material-ui/core';
import useUserRole from '../../../../hooks/useUserRole';

const validationSchema = yup.object({
    parentCategoryId: yup.string('Choose Parent Category').required('Parent Category is required'),
    title: yup.string('Enter Title').required('Title is required').trim(),
    description: yup.string().optional().trim(),
    status: yup.string('Choose Status').required('Status is required')
});

const STATUS_ITEMS = [
    { id: 0, status: 'ACTIVE' },
    { id: 1, status: 'INACTIVE' }
];

export default function AddBookCategory(props) {
    useUserRole({ roleList: ['ADMIN', 'SUPER_ADMIN'] });
    const { openAddSubCategory, setOpenAddSubCategory, isEditData } = props;
    const classes = useStyles();
    const dispatch = useDispatch();
    const { allParentCategories } = useSelector((state) => state?.category);
    const parentCategoryList = allParentCategories?.content;

    const initialValues = {
        title: isEditData?.title || '',
        description: isEditData?.description || '',
        status: isEditData?.status || 'ACTIVE',
        parentCategoryId: isEditData?.parentCategoryId || ''
    };
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: initialValues
    });

    const { reset, handleSubmit } = methods;

    const handlePopupClose = () => {
        setOpenAddSubCategory(false);
        reset();
    };

    const onSubmit = async (data) => {
        const payload = {
            parentCategoryId: data?.parentCategoryId,
            title: data?.title,
            description: data?.description,
            status: data?.status
        };
        if (isEditData) {
            dispatch(
                updateCategory(payload, isEditData.id, () => {
                    dispatch(getAllCategory());
                })
            );
            handlePopupClose();
        } else {
            dispatch(
                addCategory(payload, () => {
                    dispatch(getAllCategory());
                })
            );
            handlePopupClose();
        }
    };

    return (
        <>
            <Dialog open={openAddSubCategory} classes={{ paper: classes.dialog }}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}>{isEditData ? 'Update Sub Category' : 'Add Sub Category'} </Typography>
                        <Link to="#" onClick={handlePopupClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                    <DialogContent>
                        <Grid container>
                            <Grid item xs={12}>
                                <label>
                                    Select Parent Category <span className={classes.mandatory}>*</span>
                                </label>
                                <RHFSelect name="parentCategoryId" label="">
                                    <option value="">Select Parent Category</option>
                                    {parentCategoryList?.map((item, index) => (
                                        <option key={index} value={item?.id}>
                                            {item?.title}
                                        </option>
                                    ))}
                                </RHFSelect>
                                <br /> <br />
                                <label>
                                    Title <span className={classes.mandatory}>*</span>
                                </label>
                                <RHFTextField name="title" placeholder="Title" />
                                <br /> <br />
                                <label> Category Description </label>
                                <RHFTextField name="description" placeholder="Description" />
                                <br /> <br />
                                <label>
                                    Status <span className={classes.mandatory}>*</span>
                                </label>
                                <RHFSelect name="status" label="">
                                    {STATUS_ITEMS?.map((item, index) => (
                                        <option key={index} value={item?.status}>
                                            {item?.status}
                                        </option>
                                    ))}
                                </RHFSelect>
                            </Grid>
                        </Grid>
                    </DialogContent>
                    <Divider style={{ marginTop: 15 }} />
                    <DialogActions className={classes.dialogAction}>
                        <Button variant="outlined" color="secondary" className={classes.buttonCancel} onClick={handlePopupClose}>
                            Close
                        </Button>
                        <Button variant="contained" type="submit" className={classes.buttonSubmit}>
                            {isEditData ? 'Update' : 'Add'}
                        </Button>
                    </DialogActions>
                </FormProvider>
            </Dialog>
        </>
    );
}
